<template>
  <el-card class="box-card">
    <el-form :inline="true" class="demo-form-inline">
      <el-form-item label="username">
        <el-input placeholder="username" v-model="param.username"></el-input>
      </el-form-item>
      <el-form-item label="firstName">
        <el-input placeholder="firstName" v-model="param.firstName"></el-input>
      </el-form-item>
      <el-form-item label="lastName">
        <el-input placeholder="lastName" v-model="param.lastName"></el-input>
      </el-form-item>
      <el-form-item label="address">
        <el-input placeholder="address" v-model="param.address"></el-input>
      </el-form-item>
      <el-form-item label="birthdate">
        <el-date-picker value-format="yyyy-MM-dd" placeholder="birthdate" v-model="param.birthdate"></el-date-picker>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="searchForm">Query</el-button>
      </el-form-item>
    </el-form>
    <el-table
        border
        ref="multipleTable"
        :data="tableList"
        tooltip-effect="dark"
        style="width: 100%"
    >
      <el-table-column
          align="center"
          label="username"
          prop="username"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="firstName"
          prop="firstName"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="lastName"
          prop="lastName"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="address"
          prop="address"
          sortable
      >
      </el-table-column>
      <el-table-column
          align="center"
          label="birthdate"
          prop="birthdate"
          sortable
      >
      </el-table-column>
    </el-table>
    <el-pagination
        background
        layout="prev, pager, next"
        :total="total"
        @current-change="changePage"
    >
    </el-pagination>
  </el-card>
</template>

<script>
export default {
  data() {
    return {
      param: {
        username: '',
        firstName: '',
        lastName: '',
        address: '',
        birthdate: '',
      },
      tableList: [],
      pageInfo: {
        pageNum: 1,
        pageSize: 10,
      },
      total: 0
    }
  },
  mounted() {
    this.getTableList(this.pageInfo)
  },
  methods: {
    getTableList(){
      this.$axios.post('/users/findAll', Object.assign(this.param, this.pageInfo)).then(res => {
        this.tableList = res.data.rows;
        this.total = res.data.count;
      })
    },
    searchForm(){
      this.pageInfo.pageNum = 1;
      this.getTableList();
    },
    changePage(e){
      this.pageInfo.pageNum = e
      this.getTableList()
    }
  },
}
</script>

<style scoped lang="scss">
.el-pagination {
  margin-top: 10px;
}
</style>

